<template>
  <div class="login_container">
    <!--xz/Login.vue-->
    <!---->
    <mt-field :placeholder="unameHolder" v-model="uname" class="myinput"></mt-field>
    <!---->
    <mt-field :placeholder="upwdHolder" v-model="upwd" class="myinput"
    type="password"></mt-field>
    <!---->
    <mt-button size="large" class="mybutton" @click="login"></mt-button>
  </div>  
</template>
<script>
 export default {
   data(){
     return {
       unameHolder:"",
       upwdHolder:"",
       uname:"",
       upwd:""
     }
   },methods:{
     login(){
       //
       //1:
       var u = this.uname;
       var p = this.upwd;
       //2:
       //  3~12
var reg = /^[a-z0-9_]{3,12}$/i;
    //3:  53
    if(!reg.test(u)){
     this.$toast("");
    return;
    }
    //4:   
   if(!reg.test(p)){
    this.$toast("");
    return;
   }
   //5:ajax axios
   var url = "login";
   var obj = {uname:u,upwd:p};
   this.axios.get(url,{params:obj}).then(
     result=>{
     //1:
     //2:code:>0 Home1
     if(result.data.code > 0){
        this.$router.push("/Home1");
     }else{
        this.$messagebox("","");
     }
     //3:xz/Home1.vue
     //4:code<0
   });
   }
   }
 }   
</script>
<style scoped>
 .login_container{
   padding-top:40px;
   background-color:#ddd;
 }
</style>